.m-loading {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  height: 2rem;
}

.m-loading em {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: end;
      align-items: flex-end;
  width: .85rem;
  height: .8rem;
  margin-right: .25rem;
}

.m-loading em i {
  width: .1rem;
  height: .8rem;
  background: #73D9D9;
}

.m-loading em i:nth-child(1) {
  height: .4rem;
  -webkit-animation: mLoadingMove1 .6s linear infinite;
          animation: mLoadingMove1 .6s linear infinite;
}

.m-loading em i:nth-child(2) {
  height: .8rem;
  -webkit-animation: mLoadingMove2 .6s linear infinite;
          animation: mLoadingMove2 .6s linear infinite;
}

.m-loading em i:nth-child(3) {
  height: .6rem;
  -webkit-animation: mLoadingMove3 .6s linear infinite;
          animation: mLoadingMove3 .6s linear infinite;
}

.m-loading em i:nth-child(4) {
  height: .4rem;
  -webkit-animation: mLoadingMove1 .6s linear infinite;
          animation: mLoadingMove1 .6s linear infinite;
}

@-webkit-keyframes mLoadingMove1 {
  0% {
    height: .4rem;
  }

  50% {
    height: .8rem;
  }

  100% {
    height: .4rem;
  }
}

@keyframes mLoadingMove1 {
  0% {
    height: .4rem;
  }

  50% {
    height: .8rem;
  }

  100% {
    height: .4rem;
  }
}

@-webkit-keyframes mLoadingMove2 {
  0% {
    height: .8rem;
  }

  50% {
    height: .4rem;
  }

  100% {
    height: .8rem;
  }
}

@keyframes mLoadingMove2 {
  0% {
    height: .8rem;
  }

  50% {
    height: .4rem;
  }

  100% {
    height: .8rem;
  }
}

@-webkit-keyframes mLoadingMove3 {
  0% {
    height: .6rem;
  }

  50% {
    height: .8rem;
  }

  100% {
    height: .4rem;
  }
}

@keyframes mLoadingMove3 {
  0% {
    height: .6rem;
  }

  50% {
    height: .8rem;
  }

  100% {
    height: .4rem;
  }
}


